// Functional imports
import { HeaderSliceName } from "../store/header-slice";
import { useSelector } from "react-redux";

/**
 * Checks whether the user is allowed to access the tab against the current headers 
 * @param {String} tabEndpoint 
 * @returns {[isAllowedToAccessTab: boolean, allowedTabs: [(Map<string, string>)]]}
 */
function useTabAccessVerifier(tabEndpoint){
    const allowedTabs = useSelector(state => state[HeaderSliceName]).appTabs;
    const isAllowedToAccessTab = allowedTabs.filter(tab => tab.endpoint === tabEndpoint);

    return [isAllowedToAccessTab.length > 0, allowedTabs];
}

export default useTabAccessVerifier;