// Functional imports
import cssNameSimplifier from "../../../utility/css-name-simplifier";
import { useEffect, useState, lazy, Suspense } from "react";
import { useNavigate } from "react-router-dom";
import useTabAccessVerifier from "../../../utility/tab-access-verifier";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { useDispatch } from "react-redux";
import isEmpty from "../../../utility/is-empty";
import { AlertSliceActions } from "../../../store/alert-slice";
import { HeaderSliceName } from "../../../store/header-slice";
import { useSelector } from "react-redux";

// Components Imports
import TableView from './TableView';
import { IconButton } from "@mui/material";
import AddNew from "@mui/icons-material/Add";
import Loading from "../../../componets/Loading/Loading";
import Prompt, { defaultPromptState } from '../../../componets/Prompt/Prompt';

// Style imports
import importStyles from './ViewAll.module.css';

// Component Imports (Lazy)
const EditSource    = lazy(() => import('./EditSource'));

const styles = cssNameSimplifier(importStyles);


function ViewAll(props){
    const dispatch                          = useDispatch();
    const sendRequest                       = useHttpClientWithNavigate();
    const [isAllowedToAccessTab]            = useTabAccessVerifier('view');
    const [sourceData, setSourceData]       = useState([]);
    const [sourceToEdit, setSourceToEdit]   = useState(null);
    const [promptState, setPromptState]     = useState(defaultPromptState);
    const navigate                          = useNavigate();
    const headerSlice                       = useSelector(state => state[HeaderSliceName]);
    const { isEditable }                    = headerSlice.appPermissions;


    if(!isAllowedToAccessTab){
        navigate('/error', { state: { status: 403, msg: 'Your not allowed to access \'View\' tab' } })
    }

    useEffect(() => {
        (async function(){
            try{
                const url = props.baseURL + '/retrive-source-with-subsource';
                const sourceData = await sendRequest({ url });
                setSourceData(sourceData);
            }catch( err ){
                dispatch( AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch booking form data!', title: 'Request Failed!', severity: 'error' }) )
            }
        })();
    }, [dispatch, props.baseURL, sendRequest]);


    function handelSourceEditRequest(id){
        const sourceToEdit = sourceData.filter(source => source.id === id);

        if(isEmpty(sourceData) || sourceToEdit.length <= 0){
            return dispatch( AlertSliceActions.showAlert({ message: 'No source data found!', title: 'Edit Request Failed', severity: 'error' }) );
        }

        setSourceToEdit({ ...sourceToEdit[0], type: 'UPDATE' });
    }

    function promptCallbackforSourceDelete(response){
        if(!response){
            return setPromptState(defaultPromptState);
        }

        setPromptState(prev => {
            const sourceId = prev.other.id;
            const url = props.baseURL + `/${sourceId}/delete-source`;
            const method = 'POST';

            sendRequest({ url, method })
            .then( _ => {
                setSourceData(prev => {
                    return prev.filter(source => source.id !== sourceId);
                });

                dispatch( 
                    AlertSliceActions.showAlert({ 
                        message: `Source '${prev.other.label}' deleted successfully!`, 
                        title: 'Request Sucessful!', 
                        severity: 'success' 
                    }) 
                );
            })
            .catch(err => {
                dispatch( 
                    AlertSliceActions.showAlert({ 
                        message: err.msg || 'Source deletion failed', 
                        title: 'Request Failed!', 
                        severity: 'error' 
                    }) 
                );
            })

            return defaultPromptState;
        });
    }

    function editSourceRequestsHandeler({ type, action }){
        if(type === 'CLOSE_MODAL'){
            setSourceToEdit(null);
        }

        if(type === 'FORM_SUBMISSION'){
            const url = props.baseURL + ((sourceToEdit.type === 'CREATE_NEW')? '/create-source' : `/${sourceToEdit.id}/update-source`);
            const method = 'POST';
            const body = new URLSearchParams(action.data);

            sendRequest({ url, method, body })
            .then(res => {
                if(sourceToEdit.type === 'CREATE_NEW'){ 
                    setSourceData(prev => { 
                        prev.push(res); 
                        return prev;
                    });
    
                    dispatch( 
                        AlertSliceActions.showAlert({ 
                            message: 'Source & Subsource created successfully!', 
                            title: 'Success', 
                            severity: 'success' 
                        }) 
                    );
                } else {
                    setSourceData(
                        sourceData.map(source => (source.id !== res.id)? source : res)
                    );

                    dispatch( 
                        AlertSliceActions.showAlert({ 
                            message: 'Source & Subsource updated successfully!', 
                            title: 'Success', 
                            severity: 'success' 
                        }) 
                    );
                }

                setSourceToEdit(null);
            })
            .catch(err => {
                dispatch( 
                    AlertSliceActions.showAlert({ 
                        message: err.msg || 'Failed to ' + ((sourceToEdit.type === 'CREATE_NEW')? 'create' : 'update'), 
                        title: 'Request Failed', 
                        severity: 'error' 
                    }) 
                );
            })
        }
    }

    function handelSourceDeleteRequest(id){
        const label = sourceData.filter(s => s.id === id)[0].label;

        setPromptState({ 
            callback: promptCallbackforSourceDelete,
            title: 'Request for confirmation', 
            desc: `Are you sure you want to delete '${label}' source along with all the sub sources?`,
            note: '*Note: Source attached to booking form cannot be deleted', 
            disagreeAltText: 'No', 
            agreeAltText: 'Yes', 
            open: true, 
            other: { id, label }
        });
    }

    return (
        <div className={styles.viewContainer}>
            
            {
                isEditable &&
                <div className={styles.toolBar}>
                    <div className={styles.left}>
                    </div>

                    <div className={styles.right}>
                        <IconButton onClick={e => setSourceToEdit({ type: 'CREATE_NEW' })}>
                            <AddNew />
                        </IconButton>
                    </div>
                </div>
            }

            <TableView 
                baseURL={props.baseURL} 
                sourceData={sourceData} 
                handelSourceEditRequest={handelSourceEditRequest}
                handelSourceDeleteRequest={handelSourceDeleteRequest}
            />

            <Suspense fallback={<Loading fallback />}>
            {
                Boolean(sourceToEdit) &&
                <EditSource 
                    sourceData={sourceToEdit} 
                    requstHandler={editSourceRequestsHandeler}
                />
            }
            </Suspense>

            <Suspense fallback={<Loading fallback />}>
                <Prompt 
                    open={promptState.open}  
                    title={promptState.title}
                    desc={promptState.desc}
                    note={promptState.note}
                    callback={promptState.callback}
                    agreeAltText={promptState.agreeAltText}
                    disagreeAltText={promptState.disagreeAltText}
                />
            </Suspense>
        </div>
    );
}

export default ViewAll;