// Functional Imports
import { useState } from 'react';
import { styled } from '@mui/material/styles';
import { useEffect } from "react";
import cssNameSimplifier from '../../../utility/css-name-simplifier';
import { useSelector } from 'react-redux';
import isEmpty from '../../../utility/is-empty';
import moment from 'moment';
import { HeaderSliceName } from '../../../store/header-slice';
import { Link } from 'react-router-dom';

// Component Imports
import { Table, TableBody, TableCell, tableCellClasses, TableContainer, TableHead, TableRow, Paper, Menu, IconButton, alpha, MenuItem } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import MoreVertIcon from '@mui/icons-material/MoreVert';

// Style Imports
import importStyles from './TableView.module.css';

const styles = cssNameSimplifier(importStyles);

// Table style config
const StyledTableCell = styled(TableCell)(({ theme }) => ({
	[`&.${tableCellClasses.head}`]: {
		backgroundColor: theme.palette.common.black,
		color: theme.palette.common.white,
	},
	[`&.${tableCellClasses.body}`]: {
		fontSize: 14,
	},
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
	'&:nth-of-type(odd)': {
		backgroundColor: theme.palette.action.hover,
	},
	// hide last border
	'&:last-child td, &:last-child th': {
		border: 0,
	},
}));

const StyledMenu = styled(
    props => (
    <Menu
        elevation={0}
        anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
        }}
        transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
        }}
        {...props}
    />
    ))(({ theme }) => ({
        '& .MuiPaper-root': {
            borderRadius: 6,
            marginTop: theme.spacing(1),
            minWidth: 180,
            color:
                theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
            boxShadow:
                'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
            '& .MuiMenu-list': {
                padding: '4px 0',
            },
            '& .MuiMenuItem-root': {
                '& .MuiSvgIcon-root': {
                    fontSize: 18,
                    color: theme.palette.text.secondary,
                    marginRight: theme.spacing(1.5),
                },
                '&:active': {
                    backgroundColor: alpha(
                        theme.palette.primary.main,
                        theme.palette.action.selectedOpacity,
                    ),
                },
            },
        },
})
);

function TableView(props){
    const [sourceData, setSourceData]       = useState([]);
    const headerSlice                       = useSelector(state => state[HeaderSliceName].appPermissions);
    const { isEditable }                    = headerSlice;
    const [menuState, setMenuState]         = useState({ anchor: null, sourceId: null })

    useEffect(() => {
        setSourceData(props.sourceData);
    }, [props.sourceData]);

    function handelSourceEditRequest(e){
        props.handelSourceEditRequest(menuState.sourceId);
        setMenuState({ anchor: null, sourceId: null });
    }

    function handelSourceDeleteRequest(e){
        props.handelSourceDeleteRequest(menuState.sourceId);
        setMenuState({ anchor: null, sourceId: null });
    }

    return (
        <>
            <div className={`${styles.tableContainer} ${(isEditable)? styles.allowEditing : ''}`}>
                <TableContainer component={Paper}>
                    <Table sx={{ minWidth: 700 }} aria-label="customized table">
                        <TableHead>
                            <TableRow>
                                <StyledTableCell align="left">Index</StyledTableCell>
                                <StyledTableCell>Source</StyledTableCell>
                                <StyledTableCell>Sub Source</StyledTableCell>
                                <StyledTableCell align="center">Created Date Time</StyledTableCell>
                                <StyledTableCell align="center">Created By</StyledTableCell>
                                <StyledTableCell align="center">Updated By</StyledTableCell>
                                <StyledTableCell></StyledTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                        {   
                            !isEmpty(sourceData) && 
                            sourceData.map((row, index) => (
                                <StyledTableRow key={row.id} className={styles.dataRow}>
                                    <StyledTableCell>
                                        {++index}
                                    </StyledTableCell>

                                    <StyledTableCell component="th" scope="row">
                                        {row.label}
                                    </StyledTableCell>
                                    
                                    <StyledTableCell>
                                        {row.subsources.map(ss => ss.label).join(', ')}
                                    </StyledTableCell>
                                    
                                    <StyledTableCell align="center">
                                        {( moment(row.createdAt) !== 'Invalid Date') && moment(row.createdAt).format('DD/MM/YYYY HH:MM A')}
                                    </StyledTableCell>
                                    
                                    <StyledTableCell align="center">
                                        <Link to={`/profile/${row.SourceCreatedBy.id}/info`}> {row.SourceCreatedBy.name}</Link>
                                    </StyledTableCell>
                                    
                                    <StyledTableCell align="center">
                                        <Link to={`/profile/${row.SourceUpdatedBy.id}/info`}> {row.SourceUpdatedBy.name}</Link>
                                    </StyledTableCell>

                                    <StyledTableCell align="center">
                                        <IconButton onClick={e => setMenuState({ anchor: e.currentTarget, sourceId: row.id })}>
                                            <MoreVertIcon />
                                        </IconButton>
                                    </StyledTableCell>
                                </StyledTableRow>
                            ))
                        }
                        </TableBody>
                    </Table>
                    {
                        isEmpty(sourceData) && 
                        <h3 className={styles.noDataFound}>No Data Found</h3>
                    }
                </TableContainer>
            </div> 

            <StyledMenu
                id="source-menu"
                anchorEl={menuState.anchor}
                open={Boolean(menuState.sourceId) && isEditable}
                onClose={e => setMenuState({ anchor: null, sourceId: null })}
            >
                <MenuItem onClick={handelSourceEditRequest}>
                    <EditIcon />
                    Edit
                </MenuItem>
                <MenuItem onClick={handelSourceDeleteRequest}>
                    <DeleteIcon />
                    Delete
                </MenuItem>
            </StyledMenu>
        </>
    );
}

export default TableView;