// Functional Imports
import { useState, useEffect } from "react";

// Component Imports
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from "@mui/material";

// Style Imports

const defaultPromptState = { callback: null, title: '', desc: '', disagreeAltText: '', agreeAltText: '', note: '', open: false, other: {} };

function Prompt(props) {
    const [dialogOpen, setDailogOpen] = useState(false);

    useEffect(() => {
        setDailogOpen(props.open);
    }, [props.open])

    function handelDisagree(){
        props.callback && props.callback(false);
    }

    function handelAgree(){
        props.callback && props.callback(true);
    }

    function handleClose(){
        handelDisagree();
    }

    return (
        dialogOpen &&
        <Dialog
            open={true}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {props.title}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {props.desc}
                </DialogContentText>
                {
                    props.note &&
                    <DialogContentText id="alert-dialog-description">
                        {props.note}
                    </DialogContentText>
                }
            </DialogContent>
            <DialogActions>
                <Button onClick={handelDisagree}>{props.disagreeAltText || 'Disagree'}</Button>
                <Button onClick={handelAgree}>
                {props.agreeAltText || 'Agree'}
                </Button>
            </DialogActions>
        </Dialog>
    )
}

export default Prompt;
export { defaultPromptState };